$(function () {
    sessionStorage.removeItem("hfCateID");
    sessionStorage.removeItem("hfCateName");
    sessionStorage.removeItem("rbLevel");
    sessionStorage.removeItem("tbName");
    sessionStorage.removeItem("tbDesp");
    sessionStorage.removeItem("rbLevel");
    sessionStorage.removeItem("hash");
});

angular.module("MyApp", ["PageM","ToolM", "cfp.hotkeys"])
         .controller("MyController", ["$scope", "PageProvider", 'ToolProvider', "hotkeys", function ($scope, PageProvider,TPM, hotkeys) {

             $scope.PM = PageProvider;
             $scope.TM = {};
             $scope.VM = {};
             $scope.VM.title = "";
             $scope.VM.selectSystem = "0";

             //$("#tmBlock").show();
             //$scope.PM.changePage("", tbData, function (data) {
             //    $scope.TM.data = data.message;
             //    history.replaceState({ "pageIndex": 1 }, document.title, location.href);
             //});
             //$scope.changePage(1);

             $scope.changePage = function (pageIndex, noHistory) {
                 $("#tmBlock").show();
                 var pgData = $scope.PM.genPageData(pageIndex);
                 var postData = {};
                 postData["rc"] = $scope.VM.selectSystem; 
                 postData = $scope.PM.filterPageData(pgData, postData);
                 $scope.PM.changePage("/System/PowerM/RolePowerSettingOP.aspx", postData, function (data) {
                     $scope.TM.data = data.message;
                     $scope.$apply(function () { });

                     //if (history.pushState && noHistory == undefined) {
                     //    if (pageIndex > 1 || location.hash.length > 0) {
                     //        history.pushState({ "pageIndex": pageIndex }, document.title, location.href.split("?")[0] + "?#h");
                     //    }
                     //    else {
                     //        history.replaceState({ "pageIndex": 1 }, document.title, location.href);
                     //    }
                     //}
                 });
             };

             hotkeys.add({
                 combo: 'enter',
                 description: 'Description goes here',
                 allowIn: ['INPUT',"BUTTON", 'SELECT', 'TEXTAREA'],
                 callback: function (event, hotkey) {
                     var focusType = document.activeElement.type;
                     if (focusType == "text" || focusType == "select-one")
                     {
                         $scope.changePage(1);
                     }
                 }
             });

             //$scope.changePage(1);

             $scope.changeName = function (record) {
                 var data = $scope.save() || "";

                 var obj = {};
                 obj['i'] = record["I"];
                 obj['hash'] = encodeURIComponent(data);
                 TPM.doPOST("/System/PowerM/RolePowerSetting_Detail.aspx", obj, document.querySelector("#MyForm"));
                 //location.href = "/System/PowerM/RolePowerSetting_Update.aspx?i=" + record["I"];
             };

             $scope.changePower = function (record) {
                 var data = $scope.save() || "";

                 var obj = {};
                 obj['i'] = record["I"];
                 obj['hash'] = encodeURIComponent(data);
                 TPM.doPOST("/System/PowerM/RolePowerSetting_ChangePower.aspx", obj, document.querySelector("#MyForm"));
                 //location.href = "/System/PowerM/RolePowerSetting_UpdatePower.aspx?i=" + record["I"];
             };

             $scope.changeSystem = function (record) {
                 $scope.changePage(1);
             };

             $scope.changeAdd = function (record) {
                 var data = $scope.save() || "";

                 var obj = {};
                 obj['sc'] = $scope.VM.selectSystem;
                 obj['hash'] = encodeURIComponent(data);
                 TPM.doPOST("/System/PowerM/RolePowerSetting_Add.aspx",obj,document.querySelector("#MyForm"));
                 //location.href = "/System/PowerM/RolePowerSetting_UpdatePower.aspx?i=" + record["I"];
             };

             $scope.save = function () {
                 var data = {};
                 data["selectSystem"] = $scope.VM.selectSystem;
                 data['pgNow'] = $scope.PM.pgNow;
                 TPM.setData(data);
                 return JSON.stringify(data);
             };

             if (location.hash.length > 0) {
                 var data = TPM.getData();
                 $scope.VM.selectSystem = data.selectSystem;
                 $scope.changePage(data.pgNow);
             }
             else {
                 $scope.changePage(1);
             }
}]);

